.advantages {
  background: $color-gost-white;
  padding: 121px 60px 103px;

  @include vp-1365 {
    padding: 100px 0 84px;
  }

  @include vp-767 {
    padding: 70px 0 34px;
    text-align: center;
  }
}

.advantages .container{
  @include vp-767 {
    padding: 0;
  }
}

.advantages h2 {
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 0.03em;
  text-align: center;
  margin: 0;
  margin-bottom: 50px;
  color: $color-sapphire;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
  }

  @include vp-767 {
    margin-bottom: 55px;
    font-size: 32px;
    line-height: 32px;
  }
}

.advantages__wrapper ul {
  list-style: none;
  font-family: $ff-roboto-condensed;
  padding: 0;

    display: grid;
    grid-template-columns: repeat(3, 340px 240px 340px);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 60px;
    grid-row-gap: 116px;


    .advantages__item--square { grid-area: 1 / 1 / 2 / 2; }
    .advantages__item--comfort { grid-area: 2 / 1 / 3 / 2; }
    .advantages__item--blue { grid-area: 1 / 2 / 2 / 3; }
    .advantages__item--blue{ grid-area: 1 / 2 / 3 / 3; }
    .advantages__item--parking{ grid-area: 1 / 3 / 2 / 4; }
    .advantages__item--experience { grid-area: 2 / 3 / 3 / 4; }

    @include vp-1365 {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(1, 92px 262px 92px);
      grid-column-gap: 51px;
      grid-row-gap: 40px;

      .advantages__item--square { grid-area: 1 / 1 / 2 / 2; }
      .advantages__item--comfort { grid-area: 3 / 1 / 4 / 2; }
      .advantages__item--blue { grid-area: 2 / 1 / 3 / 2; }
      .advantages__item--blue{ grid-area: 2 / 1 / 3 / 3; }
      .advantages__item--parking{ grid-area: 1 / 2 / 2 / 3; }
      .advantages__item--experience { grid-area: 3 / 2 / 4 / 3; }
    }

    @include vp-767 {
      display: flex;
      flex-direction: column;
      gap: 35px;
    }
}

.advantages__number span {
  font-size: 70px;
  font-weight: 700;
  line-height: 70px;
  margin: 0;

  @include vp-767 {
    font-size: 64px;
    line-height: 64px;
  }
}

.advantages__item--square span {
  background: linear-gradient(128deg, #D10A42 0%, #0041CA 45.58%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.advantages__item--square {
  position: relative;
}

.advantages__item--square::after {
  content: "";
  position: absolute;
  width: 340px;
  border: 1px solid #d10a4233;
  top: 173px;

  @include vp-1365 {
    display: none;
  }
}

.advantages__item--comfort span {
  background: linear-gradient(142deg, #D10A42 0%, #0041CA 88.2%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.advantages__item--parking span {
  background: linear-gradient(142deg, #D10A42 0%, #0041CA 88.2%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.advantages__item--parking {
  position: relative;

  @include vp-767 {
    margin-bottom: 5px;
  }
}

.advantages__item--parking::after {
  content: "";
  position: absolute;
  width: 340px;
  border: 1px solid #d10a4233;
  top: 173px;

  @include vp-1365 {
    display: none;
  }
}

.advantages__item--experience span {
  background: linear-gradient(143deg, #D10A42 0%, #0041CA 87.33%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.advantages__number p {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  margin: 0;
  color: #2e35ac;
}

.advantages__item {
  display: flex;
  gap: 30px;

  @include vp-1365 {
    gap: 14px;
  }

  @include vp-767 {
    gap: 10px;
    flex-direction: column;
  }
}

.advantages__number {
  display: flex;
  flex-direction: column;
}

.advantages__text-container h3 {
  margin: 0;
  color: $color-sapphire;
  font-size: 22px;
  font-weight: 400;
  line-height: 22px;
  margin-bottom: 7px;

  @include vp-1365 {
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 12px;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 20px;
  }
}

.advantages__text-container p {
  margin: 0;
  color: $color-sapphire;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;

  @include vp-767 {
    font-size: 14px;
    line-height: 22px;
  }
}

.advantages__item--blue {
  background-color: $color-resolution-blue;
  color: $color-default-white;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 240px;
  text-align: center;
  padding: 70px 34px 79px;
  margin-top: 4px;

  @include vp-1365 {
    min-width: 400px;
    margin: 0 auto;
    padding: 35px 95px;
  }

  @include vp-767 {
    min-width: 320px;
    width: 100%;
    padding: 30px 0 39px;
    text-align: center;
    margin-bottom: 5px;
  }
}

.advantages__item--blue p {
  color: $color-default-white;
}

.advantages__item--blue h3 {
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;
  margin: 0;
  margin-bottom: 5px;

  @include vp-1365 {
    font-size: 24px;
    line-height: 24px;
    margin-bottom: 12px;
  }

  @include vp-767 {
    margin-bottom: 10px;
    font-size: 22px;
    line-height: 22px;
  }
}

.advantages__item--blue span {
  margin-bottom: 12px;

  @include vp-1365 {
    margin-bottom: 25px;
  }

  @include vp-767 {
    margin-bottom: 6px;
  }
}

.advantages__br {
  display: none;

  @include vp-767 {
    display: block;
  }
}
